@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap");

/* ======== Varirables ======== */
:root {
  --header-height: 3rem;
  /* ======== Colors ======== */
  --hue: 210;
  --sat: 20%;
  --title-color: hsl(var(--hue), var(--sat), 90%);
  --title-color-dark: hsl(var(--hue), var(--sat), 100%);
  --text-color: hsl(var(--hue), var(--sat), 80%);
  --body-color: hsl(var(--hue), var(--sat), 10%);
  --container-color: hsl(var(--hue), var(--sat), 20%);
  --main-color: #00acc1;
  --google-color: #db4437;
  --facebook-color: #3b5998;
  --yahoo-color: #400191;

  /* ======== Font and typography ======== */
  --body-font: "Poppins", sans-serif;

  /* .5rem = 8px | 1rem = 16px ... */

  --big-font-size: 3.5rem;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  /* ======== Font weight ======== */

  --font-normal: 400;
  --font-meduim: 500;
  --font-semi-bold: 600;

  /* ======== Margenes Bottom ======== */
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;
  /* ======== Z-index ======== */
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/* ======== BASE======== */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  background-color: var(--body-color);
  color: var(--text-color);
  transition: all 0.5s ease;
}

/* Responsive Typography */
@media screen and (max-width: 993px) {
  :root {
    --big-font-size: 2.75rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h1-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813;
    --smaller-font-size: 0.75rem;
  }
}
h1,
h2,
h3 {
  color: var(--main-font-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}
a {
  text-decoration: none;
  cursor: pointer;
}
button {
  cursor: pointer;
  border: none;
  outline: none;
}
p {
  color: var(--text-color);
}
img {
  max-width: 100%;
  height: auto;
}

/* ======== RESUSABLE CSS CLASSES======== */
.section {
  /* padding: 6rem 0 2rem; */
  min-height: 100vh;
  max-width: 100vw;
}
.section__title {
  font-size: var(--h1-font-size);
  color: var(--main-font-color);
}

.section__subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: 4rem;
}
.section__title,
.section__subtitle {
  text-align: center;
}
/* ========  LAYOUT ======== */

.container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: var(--mb-1-5);
  padding-right: var(--mb-1-5);
}
.grid {
  gap: 1.5rem;
}
/* ======== BUTTONS ======== */

.button {
  display: inline-block;
  background-color: var(--title-color-dark);
  color: var(--body-color);
  padding: 1rem 2rem;
  font-weight: var(--font-meduim);
  box-shadow: var(--box-shadow-img);
}
.button:hover {
  background-color: var(--title-color);
}

.button__icon {
  margin-left: var(--mb-0-5);
}
.button--flex i:hover {
  transform: scale(1.2);
}
.button--flex-1 {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.button--flex-2 {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-left: 1rem;
}

.button--small {
  padding: 0.5rem 1rem;
  font-weight: var(--font-meduim);
}
/* ======== BREAKPOINTS======== */

/* Large device */
@media screen and (max-width: 992px) {
  .container {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }
  .button {
    padding: 1rem 1.75rem;
  }
  .button__icon {
    width: 22px;
    height: 22px;
  }
}
/* Meduim device */
@media screen and (max-width: 768px) {
  body {
    margin: 0 0 var(--header-height) 0;
  }
  .section {
    padding: 2rem 0 4rem;
  }
  .section__subtitle {
    margin-bottom: var(--mb-3);
  }
}
/* Meduim device */
@media screen and (max-width: 576px) {
}

/* Small device */
@media screen and (max-width: 350px) {
  :root {
    --big-font-size: 2.25rem;
  }
  .container {
    margin-left: var(--mb-1);
    margin-right: var(--mb-1);
  }
}
